.Loader {
    font-size: 10px;
    margin: 50px auto;
    text-indent: -9999em;
    width: 11em;
    height: 11em;
    border-radius: 50%;
    background: #FF4C29;
    background: -webkit-linear-gradient(left, #FF4C29 10%, rgba(69, 14, 89, 0) 42%);
    background: -o-linear-gradient(left, #FF4C29 10%, rgba(69, 14, 89, 0) 42%);
    background: -webkit-gradient(linear, left top, right top, color-stop(10%, #FF4C29), color-stop(42%, rgba(69, 14, 89, 0)));
    background: linear-gradient(to right, #FF4C29 10%, rgba(69, 14, 89, 0) 42%);
    position: relative;
    -webkit-animation: load3 1.4s infinite linear;
    animation: load3 1.4s infinite linear;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
}

.Loader:before {
    width: 50%;
    height: 50%;
    background: #FF4C29;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
}

.Loader:after {
    background: #ffffff;
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

@-webkit-keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}


